import React from "react"
import { Link, useStaticQuery, graphql } from "gatsby"

const Header = () => {
  const data = useStaticQuery(
    graphql`
      query {
        site {
          siteMetadata {
            title
            description
          }
        }
      }
    `
  )
  return (
    <div class="navbarHeader sticky-top mx-3 bg-none" role="navigation">
      <nav class="navbar mx-3 navbar-expand-lg navbar-light">
        <Link class="navbar-brand" to="/">
          {data.site.siteMetadata.title}
        </Link>
        <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarLinks" aria-controls="navbarLinks" aria-expanded="false" aria-label="Toggle navigation">
          <span class="navbar-toggler-icon"></span>
        </button>
        <div class="collapse navbar-collapse justify-content-end" id="navbarLinks">
          <ul class="nav navbar-nav">
              <li class="nav-item"><Link class="nav-link" to="/">Home</Link></li>
              <li class="nav-item"><Link class="nav-link" to="/projects/">Projects</Link></li>
              <li class="nav-item"><Link class="nav-link" to="/about/">About</Link></li>
              <li class="nav-item"><Link class="nav-link" to="/contact/">Contact</Link></li>
          </ul>
        </div>
      </nav>
    </div>
  )
}

export default Header